import React, { useState, useRef, useEffect } from 'react';

function ChatSection() {
  const [userMessage, setUserMessage] = useState('');
  const [chatHistory, setChatHistory] = useState([]);
  const [chatHistoryVisible, setChatHistoryVisible] = useState(false);
  const [isTyping, setIsTyping] = useState(false);
  const chatHistoryRef = useRef(null);
  const lastMessageRef = useRef(null);

  const botResponses = [
  "I've got a fantastic recipe for you! How about trying a delicious spaghetti carbonara? Here are the ingredients...",
  "Looking for a new job? Tailoring your resume and networking can significantly boost your job search. Would you like more tips on this?",
  "Creating a beautiful landscape involves planning. Start with a rough sketch and consider elements like plants, pathways, and focal points. Need more guidance?",
  "Could you share more details about the product you're searching for? That'll help me provide better assistance.",
  "I'm sorry to hear that you're feeling overwhelmed. Let's work together to find ways to manage your workload.",
  "Planning your trip to Paris, right? I'd love to recommend some attractions based on your interests!",
  ];
  const handleUserMessageChange = (e) => {
    setUserMessage(e.target.value);
    
  };

  const getRandomBotResponse = () => {
    const randomIndex = Math.floor(Math.random() * botResponses.length);
    return botResponses[randomIndex];
  };

  const handleSendMessage = async () => {
    if (userMessage.trim() === '') return;
  
    const newMessage = { text: userMessage, type: 'user' };
    setChatHistory([...chatHistory, newMessage]);
    setUserMessage('');
  
    setIsTyping(true);
  
    const botResponse = getRandomBotResponse();
    setChatHistory((prevChatHistory) => [
      ...prevChatHistory,
      { text: '', type: 'bot', isTyping: true }, // Initially empty text to simulate typing
    ]);
    setChatHistoryVisible(true);
  
    const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
    for (let i = 0; i < botResponse.length; i++) {
      await delay(25); // Adjust the delay time (in milliseconds) between each letter
      setChatHistory((prevChatHistory) => [
        ...prevChatHistory.slice(0, -1), // Remove the last element
        { text: botResponse.slice(0, i + 1), type: 'bot', isTyping: true }, // Update with letters progressively
      ]);
    }
  
    setIsTyping(false);
  
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = '44px'; // Reset the height immediately after sending a message
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSendMessage();
    }
  };

  useEffect(() => {
    if (chatHistoryVisible && chatHistoryRef.current) {
      chatHistoryRef.current.scrollTop = chatHistoryRef.current.scrollHeight;
    }
  }, [chatHistory, chatHistoryVisible]);

  const textareaRef = useRef(null); // Create a reference for the textarea element

  const handleTextareaResize = () => {
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = '44px'; // Reset the height to auto to correctly calculate scrollHeight
      textarea.style.height = `${textarea.scrollHeight}px`; // Set the height to match the scrollHeight
    }
  };

  useEffect(() => {
    handleTextareaResize(); // Initially set the correct height when component mounts
  }, []);

  return (
    <section id="Let's-Chat" className="port-mobile">
      <div className="container">
        <div className="row text-center mt-5">
          <h1 className="display-3 fw-bold text-capitalize">Let's Chat</h1>
          <div className="heading-line"></div>
        </div>
      </div>

      <div className="container">
        <div className="row pt-2 pb-2 mt-0 mb-3">
          <div className="col-md-6 border-right">
            <div className="bg-white p-3">
              <h2 className="fw-bold text-capitalize text-center">
                Type in your question below
              </h2>
            </div>
          </div>
          <div className="col-md-6">
            <div className="bg-white  text-start">
              <p className="fw-light">
                We understand that AI might not always give the answer you need, we've been there. That's why we've teamed up with experts from all fields
                to get you the right answer in case you're doubting the AI or need more help.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="chat-container chatbox">
        <div className={`chat-history${chatHistoryVisible ? ' visible' : ''}`} ref={chatHistoryRef}>
          {chatHistory.map((message, index) => (
            <div
              key={index}
              className={`chat-bubble-bot ${message.type === 'user' ? 'user-sent-bot' : 'bot'}`}
              ref={index === chatHistory.length - 1 ? lastMessageRef : null}
            >
              {message.type === 'bot' && (
                <div className="bot-message">
                  <div className="bot-avatar">
                    <img
                      className="bot-avatar"
                      src={`${process.env.PUBLIC_URL}/images/arts/TIO.webp`}
                      alt="TIO-bot"
                    />
                  </div>
                  <div className="bot-details">
                  <div className="bot-name">TIO-bot</div>
                      <div className="chat-text">{message.text}</div>
                    
                  </div>
                </div>
              )}
              {message.type !== 'bot' && <div className="chat-text">{message.text}</div>}
            </div>
          ))}
        </div>
        <div className="d-flex align-items-end" id="user-input">
        <textarea
          ref={textareaRef} // Connect the textareaRef to the textarea element
          type="text"
          placeholder="Type a message..."
          value={userMessage}
          onChange={handleUserMessageChange}
          onKeyDown={handleKeyPress}
          className="chatbot-input no-scroll" // Maintain your existing classes here
          id="user-input"
          onInput={handleTextareaResize} // Add an event listener for input changes to adjust the height
        />
        <button onClick={handleSendMessage} aria-label="Send Message" className='user-input-button'>
          <i className="fas fa-paper-plane"></i>
        </button>
        </div>
      </div>
    </section>
  );
}

export default ChatSection;